.nav {
    grid-area: nav;
    background-color: #EBBABB;
    padding: 10px;
}
.navLink {
    color: black;
    text-transform: uppercase;
    text-decoration: none;
    font-size: 14px;
    letter-spacing: 0.1em;
    transition: all 0.5s ease;
}
.linkHover:hover {
    color: rgb(168, 83, 83);
}